
var jsonp = require("jsonp");
import Panel from 'vux/src/components/panel/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

export default{
data(){
return {
title:"",
hotList:[],
start:0,
count:10,
}
},
components:{
Panel,
Swipeout,
SwipeoutItem,
SwipeoutButton,
},
methods:{
del(index){

this.hotList.splice( index,1 );
},
getData(){
var that = this;

if( this.$route.params.id == "hotList" ){
this.title = "";
jsonp('https://api.douban.com/v2/movie/in_theaters?start='+this.start+'&count='+this.count,null,function(res,data){
console.log( data );
that.hotList = data.subjects.map( (item)=>{
return {

url: item.alt,
src: item.images.small,
title: item.title,
desc: ""+item.casts.map((a)=>{ return a.name }).join("")
}
} )
})
}
},
re(){
this.start = 0;
this.count = 10;
this.$vux.toast.show({
text: '',
time: 1000,
})
localStorage.clear();
this.getData();
this.$refs.my_scroller.finishPullToRefresh();
},
loadMore:function(done){

this.start += this.count;
this.count = 3;

this.$refs.my_scroller.finishInfinite(function(){
console.log(1)
})

}
},
created(){
this.getData()
}
}



