
var jsonp = require("jsonp");
import Panel from 'vux/src/components/panel/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

export default {
data(){
return {
movieList : [],
}
},
components:{
Panel,
Swipeout,
SwipeoutItem,
SwipeoutButton,
},
methods:{
getDataById(id){
var that = this;
jsonp('https://api.douban.com/v2/movie/subject/'+id,null,function(res,data){
let obj = {};
obj.title = data.title;
obj.src = data.images.large;
obj.desc = data.summary;
obj.castsName = data.casts.map((a)=>{return a.name}).join("/");
that.movieList.push( obj );












})
},
},
created(){
this.movie = JSON.parse( localStorage.getItem( "movie" ) ) || [];
this.movieList = [];
this.movie.forEach((item)=>{
this.getDataById(item);
})
console.log( this.movieList );
}
}



